// Apply the theme
Highcharts.setOptions(Highcharts.theme);

const ElectoralMap = () => {
    const [options] = useState({
        chart: {
            animation: false
        },

        title: {
            text: "Presidential Electoral Map <em>(Kenya)</em>"
        },

        legend: {
            align: 'right',
            verticalAlign: 'top',
            // x: -100,
            // y: 1,
            floating: true,
            layout: 'vertical',
            valueDecimals: 0,
            title: {
                text: "Political Affiliation",

            }
        },

        credits: {
            enabled: false
        },

        tooltip: {
            valueSuffix: "Margin"
        },

        mapNavigation: {
            enabled: true,
            enableButtons: false
        },

        colorAxis: {
            dataClasses: [
                {
                    from: 0.0000001,
                    to: 100,
                    color: "#C40401",
                    name: "UDA"
                },
                {
                    from: -100,
                    to: -0.00000001,
                    color: "#0200D0",
                    name: "Azimio"
                },
                {
                    from: 0,
                    to: 0,
                    color: "#C0C0C0",
                    name: "Battle Ground(s)"
                }
            ]
        },
        series: [
            {
                name: "By County Difference",
                point: {
                    events: {
                        //Show the default tooltip
                        mouseOver: function () {
                            let point = this;
                            this.series.chart.update({
                                tooltip: {
                                    enabled: true,
                                    formatter: function () {
                                        let s = "";
                                        s += `<span style="color:${point.color}">●</span> <span style="font-size: 10px"> ${point.series.name}</span><br/>`;
                                        s += `${point.name}: ${point.value}<br/>`;
                                        return s;
                                    }
                                }
                            });
                        },
                        //Show the pie chart
                        click: function () {
                            let y1 = Number(this.Azimio);
                            let y2 = Number(this.UDA);
                            this.series.chart.update({
                                tooltip: {
                                    useHTML: true,
                                    enabled: true,
                                    formatter: function () {
                                        setTimeout(function () {
                                            Highcharts.chart("chart", {
                                                chart: {
                                                    type: "pie"
                                                },
                                                title: {
                                                    text: null
                                                },
                                                series: [
                                                    {
                                                        name: "Votes",
                                                        data: [
                                                            {
                                                                name: "Azimio",
                                                                color: "#0200D0",
                                                                y: y1
                                                            },
                                                            {
                                                                name: "UDA",
                                                                color: "#C40401",
                                                                y: y2
                                                            }
                                                        ],
                                                        dataLabels: {
                                                            format: "<b>{point.name}</b> {point.value:.1f}%"
                                                        }
                                                    }
                                                ]
                                            });
                                        }, 10);
                                        return '<div id="chart" style="width: 300px; height: 150px;"></div>';
                                    }
                                }
                            });
                        },
                        //Remove the tooltip
                        mouseOut: function () {
                            this.series.chart.update({
                                tooltip: {
                                    enabled: false
                                }
                            });
                        }
                    }
                },
                type: "map",
                joinBy: ["name", "name"],
                data: json,
                mapData: pathData
            },
            {
                type: "mapline",
                data: [
                    {
                        name: "path5072",
                        path: "M443,-449Z"
                    }
                ]
            }
        ]
    });

    return (
        <HighchartsReact
            highcharts={Highcharts}
            constructorType={"mapChart"}
            options={options}
        />
    );
};


export default ElectoralMap
